<template>
  <div class="about">
    <h1>This is an about page</h1>
	<hr/>

	<div class="tasks">
		<!-- <ul class="tasks-table">
			<li class="tasks-table__item">
				<h3 class="tasks-table__title">Task</h3>
			</li>
			<li class="tasks-table__item">
				<h3 class="tasks-table__title">Status</h3>
			</li>
		</ul> -->

		<form class="form" @submit.prevent="setTasksObj">
			<input type="text" v-model="inputTextTask" placeholder="Enter task name">
			<input type="submit" name="send-task" value="Send task">
		</form>

		<ul class="tasks-list">
			<div class="tasks-list__filter">
				<p>
					Hide completed tasks : <input v-model="hideDoneTask" type="checkbox">
				</p>
			</div>
			<li
				class="tasks-list__wrap"
				v-for="(item, index) in statusLabel"
				v-bind:key="index"
			>
				<div class="tasks-list__item">
					<p>{{ item.name }} </p>
					<p> <input v-model="item.status.done" type="checkbox"> - {{ item.status.done }}</p>
				</div>
			</li>
		</ul>
		<div class="col text-center">
			<button
				class="btn btn-sm btn-warning"
				v-on:click="deleteCompleted">
					Delete Completed
			</button>
		</div>
	</div>
  </div>
</template>


<script>
export default ({
	name: "About",
	data () {
		return {
			tasks: [],
			hideDoneTask: true,
			inputTextTask: ""
		}
	},
	computed: {
		statusLabel() {
			console.log(this.tasks);
			return this.hideDoneTask ? this.tasks.filter(t => !t.status.done) : this.tasks;
		}
	},
	methods: {
		setTasksObj() {
			const latestItem = {
				name: this.inputTextTask,
				status: { done: false}
			};
			this.tasks.push(latestItem);
			localStorage.setItem("tasks", JSON.stringify(this.tasks))

			this.inputTextTask = "";
		},
		deleteCompleted() {
			this.tasks = this.tasks.filter(t => !t.done);
			localStorage.setItem("tasks", JSON.stringify(this.tasks))
		}
	},
	created () {
		//PUSHING PERSISTANT DATA BACK TO THE SAME DATA ARRAY PROPERTY
		let locallyStoredData = localStorage.getItem('tasks') ? JSON.parse(localStorage.getItem('tasks')) : [];
		this.tasks = locallyStoredData
		console.log("CREATE:", this.tasks);
	}
})
</script>

